.education__container {
  padding: 40px 20px;
  background-color: rgba($color-default-white, 0.2);

  @include vp-768 {
    padding: 40px;
  }
}

.education h2 {
  @include font("Minecraft", 600, 35px, $color-tolopea, 60px);
  text-align: center;
  letter-spacing: 1px;
  margin-bottom: 20px;
  word-wrap: wrap;
  word-wrap: break-word;
  hyphens: auto;

  @include vp-768 {
    font-size: 45px;
  }

  @include vp-1024 {
    font-size: 50px;
  }
}

.education__list {
  list-style: none;
  margin: 0;
  padding: 0;
  color: $color-default-white;
  display: flex;
  // padding: 40px;

}

.education__item {
  position: relative;
  background-color: $color-tolopea;
  overflow: auto;
  margin: 0;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  // min-width: min-content;
  // width: 230px;
  // max-width: 274px;
  height: auto;
  border: 6px dotted $color-deep-magenta;
}

.education__item h3 {
  @include font(sans-serif, 600, 25px, ($color-tolopea, 0.8), 30px);
  display: block;
  font-style: normal;
  z-index: 2;
  bottom: 5px;
  position: absolute;
  text-align: center;

  @include vp-768 {
    font-size: 30px;
  }

  @include vp-1024 {}
}

