.education__item-image {
  // position: absolute;
  width: 100%;
  height: 100%;
  min-width: 260px;
  min-height: 294px;
  background-blend-mode: multiply;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(1px) contrast(0.6);
  background-color: rgba($color-deep-magenta, 0.3);

  &--miet-bak {
    background-position: top;
    background-image: url("../../img/miet-bak.png");
  }

  &--miet-mag {
    background-color: $color-tolopea;
  }

  &--miet-translator {
    background-image: url("../../img/miet-translator.png");
  }

  &--miet-translator {
    background-image: url("../../img/miet-translator.png");
  }

  &--html-akselerator {
    background-image: url("../../img/html-akselerator.jpg");
  }

  &--html-js {
    background-image: url("../../img/html-js.png");
  }

  &--school {
    background-image: url("../../img/school21.png");
  }

  &--dxh {
    background-image: url("../../img/dxh.png");
  }
}

.education__content-wrapper {
  position: relative;
}

.education__content-button {
  background-color: transparent;
  border: none;
}

.education__content-button svg {
  stroke: $color-tolopea;

  cursor: pointer;
  width: 10px;
  height: 19px;

  @include vp-768 {
    width: 18px;
    height: 34px;
  }
}

.education__content-button--prev {
  position: absolute;
  left: -20px;
  top: 46%;
  // left: -23;

  &:hover {
    cursor: pointer;
  }

  @include vp-768 {
    top: 44%;
    left: -33px;
  }
}

.education__content-button--next {
  position: absolute;
  right: -20px;
  top: 46%;
  // right: -23;

  @include vp-768 {
    top: 44%;
    right: -33px;
  }
}
