  font-weight: 600;
  font-size: 25px;
  font-family: sans-serif;
  color: #170D3F;
  line-height: 30px;
  letter-spacing: 1px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .experience h3 {
    font-size: 30px;
  }
}

.experience p {
  font-weight: 400;
  font-size: 19px;
  font-family: sans-serif;
  color: #170D3F, 0.8;
  line-height: 22px;
  letter-spacing: 1px;
}
@media (min-width: 768px) {
  .experience p {
    font-size: 30px;
    line-height: 28px;
  }
}

.experience__wrapper {
  position: relative;
}

.experience__item-date {
  position: relative;
}

.experience__item-date::before {
  content: "";
  position: absolute;
  background-color: #D803C6;
  left: -24px;
  top: 5px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.experience__list {
  margin-left: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.experience__item {
  margin: 50px 0;
}

.experience__item a:hover h3 {
  color: #8C8A93;
}

.footer__container {
  padding: 40px 20px;
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
}

.footer__nav-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  max-width: 250px;
}
@media (min-width: 768px) {
  .footer__nav-list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.footer__nav-item {
  margin-bottom: 20px;
  margin-right: 15px;
}

.footer__nav-item-link {
  font-weight: 400;
  font-size: 20px;
  font-family: "Minecraft";
  color: #170D3F;
  line-height: 20px;
  letter-spacing: 4px;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.3s;
}
.footer__nav-item-link:hover {
  color: #8C8A93;
}
@media (min-width: 1024px) {
  .footer__nav-item-link {
    position: relative;
  }
}

.footer__social-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 1024px) {
  .footer__social-list {
    justify-content: space-between;
  }
}

.footer__social-item {
  margin-right: 10px;
}

.footer__social-item svg {
  width: 30px;
  height: 30px;
}
@media (min-width: 768px) {
  .footer__social-item svg {
    width: 40px;
    height: 40px;
  }
}

